import NodeRSA from 'node-rsa'

// RSA-OAEP加密器
function RSAOAEPEncryptor (publicKey, message) {
  // 创建NodeRSA实例
  const key = new NodeRSA()
  key.setOptions({
    environment: 'browser',
    encryptionScheme: {
      hash: 'sha256'
    }
  })
  // 导入RSA公钥
  key.importKey(publicKey, 'pkcs8-public-pem')
  // 字符串化原始信息
  const plaintext = typeof message === 'string' ? message : JSON.stringify(message)
  // 得到加密结果
  const ciphertext = key.encrypt(plaintext, 'base64')
  // 返回Base64格式的最终密文
  return ciphertext
}

export {
  RSAOAEPEncryptor
}
