<template>
  <div id="app">
    <transition
      :enter-active-class="pageEnterClass"
      :leave-active-class="pageLeaveClass"
    >
      <keep-alive>
        <router-view />
      </keep-alive>
    </transition>
    <transition
      enter-active-class="animate__animated animate__bounceIn"
      leave-active-class="animate__animated animate__bounceOut"
    >
      <BaseButtonTry
        v-if="showBtnTry"
        id="app-btn-try"
        :trying="isTrying"
        @click.native="onBtnTryClicked"
      />
    </transition>
  </div>
</template>

<style>
@import '~animate.css';

:root {
  --max-width: 750px;
  --primary-color: #2196f3;
  --secondary-color: #e91e63;
  --icon-color: #969799;
  --border-color: #f7f8fa;
  --border-color-darker: #ebedf0;
  --background-color: #f7f8fa;
  --background-color-darker: #ebedf0;
}

* {
  position: relative;
  box-sizing: border-box;
}

#app {
  width: 100vw;
  height: 100vh;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

@media screen and (min-width: 750px) {
  .van-popup,
  .van-overlay,
  .van-nav-bar--fixed {
    left: 0;
    right: 0;
    margin: auto;
    max-width: var(--max-width);
  }

  #app {
    margin: auto;
    max-width: var(--max-width);
    border-left: 1px solid var(--border-color);
    border-right: 1px solid var(--border-color);
  }
}

#app>[id^="page-"] {
  min-width: 100%;
  min-height: 100%;
  background-color: var(--background-color);
}

#app-btn-try {
  position: absolute;
  right: 24px;
  bottom: 24px;
  z-index: 1;
}

.page-absolute {
  position: absolute !important;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
</style>

<script>
import axios from 'axios'
import {
  urlAPIGetRSACryptionKey,
  urlAPIPostAESCryptionSecret,
  urlAPIGetUserInformation,
  urlDefaultBookmarks,
  urlDefaultBackground
} from '@/api/requestURLs.js'
import { genAESCBCSecrets } from '@/utils/AESCBCCryptor.js'
import { iconDataStore, bgImgDataStore } from '@/utils/localForageStore.js'
import { axiosRenewAccessToken, axiosEncryptWithRSAOAEP, axiosDecryptWithAESCBC } from '@/utils/axiosInterceptor.js'
import { Notify } from 'vant'
import BaseButtonTry from '@/components/BaseButtonTry.vue'

export default {
  name: 'App',
  components: {
    BaseButtonTry
  },
  data () {
    return {
      pageEnterClass: '',
      pageLeaveClass: '',
      userInfoUpdated: false,
      isTrying: false,
      urls: {
        urlGetRSAPubKey: urlAPIGetRSACryptionKey,
        urlPostAESSecrets: urlAPIPostAESCryptionSecret,
        urlGetUserInfo: urlAPIGetUserInformation,
        urlGetDefaultBookmarks: urlDefaultBookmarks,
        urlGetDefaultBackground: urlDefaultBackground
      }
    }
  },
  computed: {
    isConnected () {
      return this.$store.getters.isConnected
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    },
    showBtnTry () {
      return !this.isConnected || (this.isLoggedIn && !this.userInfoUpdated)
    }
  },
  watch: {
    $route (to, from) {
      if (!from.meta.level) {
        this.pageEnterClass = ''
        this.pageLeaveClass = ''
        return
      }
      if (to.meta.level >= from.meta.level) {
        this.pageEnterClass = 'animate__animated animate__faster animate__slideInRight'
        this.pageLeaveClass = 'page-absolute animate__animated animate__faster'
        return
      }
      this.pageEnterClass = 'page-absolute animate__animated animate__faster'
      this.pageLeaveClass = 'animate__animated animate__faster animate__slideOutRight'
    },
    isConnected (value) {
      value && this.isLoggedIn && this.tryToUpdateUserInfo()
    },
    isLoggedIn (value) {
      this.isConnected && value && this.tryToUpdateUserInfo()
    }
  },
  created () {
    // 获取本地存储中的访问令牌
    this.getLocalJWTString()
    // 获取本地存储中的用户信息
    this.getLocalUserInfo()
    // 获取本地存储中的用户书签，或获取并保存默认书签信息
    localStorage.getItem('appUserBookmarks') ? this.getLocalBookmarks() : this.saveDefaultBookmarks()
    // 获取本地存储中的背景信息，或获取并保存默认背景信息
    localStorage.getItem('appUserBackground') ? this.getLocalBackground() : this.saveDefaultBackground()
  },
  async mounted () {
    // 连接服务器，建立AES加密通信
    await this.tryToConnectServer()
    // 若已登录，更新用户信息
    this.isConnected && this.tryToUpdateUserInfo()
  },
  methods: {
    /**
     * 通信加密相关
     */
    // 从后端获取RSA-OAEP公钥
    getRSAPubKey () {
      return axiosRenewAccessToken().get(this.urls.urlGetRSAPubKey)
        .then(response => response.data.payload.key)
        .catch(error => Promise.reject(error))
    },
    // 前端向后端发送加密后的信息
    postRSAEncryptedMsg (rsaPubKey, message) {
      return axiosEncryptWithRSAOAEP(rsaPubKey).post(this.urls.urlPostAESSecrets, message)
        .then(response => response)
        .catch(error => Promise.reject(error))
    },
    // 异步加密通信
    async encryptCommunication () {
      // 从后端获取RSA-OAEP公钥
      const rsaPubKey = await this.getRSAPubKey()
      // 前端生成AES-CBC初始向量与密钥
      const aesSecrets = genAESCBCSecrets()
      // 前端向后端发送加密后的信息
      const response = await this.postRSAEncryptedMsg(rsaPubKey, aesSecrets)
      // 通信成功则保存AES-CBC初始向量与密钥至状态管理中心
      this.$store.commit('setAppAESSecrets', aesSecrets)
      // 返回响应结果
      return response
    },
    // 连接服务器
    connectServer () {
      return this.encryptCommunication()
        .catch(() => {
          // 连接服务器失败则显示通知
          Notify({
            type: 'danger',
            message: '连接服务器失败'
          })
        })
    },
    // 尝试重新连接服务器
    tryToConnectServer () {
      if (this.isTrying) {
        return
      }
      this.isTrying = true
      return this.connectServer()
        .then(() => {
          this.isTrying = false
        })
    },
    /**
     * 访问令牌相关
     */
    // 获取本地存储中的JWT
    getLocalJWTString () {
      this.$store.commit('setAppJWTString', localStorage.getItem('appJWTString'))
    },
    /**
     * 用户信息相关
     */
    // 获取本地存储中用户信息
    getLocalUserInfo () {
      const userInfoStr = localStorage.getItem('appUserInfo')
      const userInfo = userInfoStr ? JSON.parse(userInfoStr) : {}
      this.$store.commit('setAppUserInfo', userInfo)
    },
    // 获取后端存储的用户信息
    getServerUserInfo () {
      return axiosDecryptWithAESCBC().get(this.urls.urlGetUserInfo)
        .then(response => response?.data?.payload)
        .catch(error => Promise.reject(error))
    },
    // 更新本地用户信息
    updateLocalUserInfo () {
      // 连接服务器并已登录后方可获取用户信息
      return this.getServerUserInfo()
        .then(userInfo => {
          this.userInfoUpdated = true
          // 更新用户个人信息至本地
          this.$store.dispatch('updateAppUserInfo', userInfo)
        })
        .catch(() => {
          this.userInfoUpdated = false
          // 更新用户信息失败的通知
          this.isLoggedIn && Notify({
            type: 'danger',
            message: '更新用户信息失败'
          })
        })
    },
    // 尝试更新本地用户信息
    tryToUpdateUserInfo () {
      if (this.isTrying) {
        return
      }
      this.isTrying = true
      return this.updateLocalUserInfo()
        .then(() => {
          this.isTrying = false
        })
    },
    /**
     * 用户书签相关
     */
    // 获取并保存默认书签
    async saveDefaultBookmarks () {
      // 获取默认书签信息
      const defBms = await axios.get(this.urls.urlGetDefaultBookmarks)
        .then(response => {
          return Promise.resolve(response?.data)
        })
        .catch(error => {
          console.error(error)
          return Promise.reject(new Error('获取默认书签信息失败'))
        })
      // 临时书签数组
      const bookmarks = []
      // 保存书签图标信息
      for (const item of defBms) {
        const iconURL = item?.iconURL?.replace('BASE_URL/', process.env.BASE_URL)
        const iconType = `image/${iconURL?.split('.').pop()}`
        const iconName = `${new Date().getTime()}.${iconType.split('/')[1]}`
        const bookmark = {
          title: item?.title,
          url: item?.url,
          iconName: undefined,
          iconSrc: undefined,
          iconFile: undefined
        }
        await axios.get(iconURL, {
          responseType: 'blob'
        })
          // 书签信息获取成功后，获取并转换图标文件
          .then(response => new Promise((resolve, reject) => {
            const iconBlob = response?.data
            if (!iconBlob?.size) {
              return reject(new Error(`invalid icon file of ${item?.title}`))
            }
            const reader = new FileReader()
            // 补全单个书签信息
            reader.addEventListener('load', () => {
              bookmark.iconName = iconName
              bookmark.iconSrc = reader.result
              bookmark.iconFile = new File([iconBlob], iconName, {
                type: iconType
              })
              return resolve()
            })
            reader.addEventListener('error', () => {
              return reject(new Error(`failed to convert icon file of ${item?.title}`))
            })
            reader.addEventListener('abort', () => {
              return reject(new Error(`icon file conversion of ${item?.title} aborted`))
            })
            reader.readAsDataURL(iconBlob)
          }).catch(error => {
            console.error(error)
          }).finally(() => {
            // 填充临时书签信息数组
            bookmarks.push(bookmark)
          }))
          .catch(() => {
            // 书签图标获取失败
            console.log(`failed to access image from ${iconURL}`)
            // 填充临时书签信息数组
            bookmarks.push(bookmark)
          })
      }
      // 保存临时书签信息至本地
      return this.$store.dispatch('saveAppUserBookmarks', bookmarks)
        .catch(error => {
          console.error(error)
          return Promise.reject(new Error('保存默认书签信息失败'))
        })
    },
    // 获取本地存储的书签
    getLocalBookmarks () {
      // 临时书签信息数组
      let localBookmarks = []
      // 获取localStorage中的书签列表
      try {
        const tempStr = localStorage.getItem('appUserBookmarks')
        const tempArr = tempStr ? JSON.parse(tempStr) : []
        localBookmarks = Array.isArray(tempArr) ? tempArr : []
      } catch (error) {
        console.error(error)
        Notify({
          type: 'danger',
          message: '获取本地书签列表失败'
        })
        return
      }
      // 建立异步请求数组，获取IndexedDB/WebSQL中的书签图标
      const promisesGetIconFiles = localBookmarks.map(item =>
        iconDataStore.getItem(item.iconName)
          .then(iconData => {
            item.iconSrc = iconData.dataURL
            item.iconFile = iconData.file
          })
          .catch(() => {
            console.error(`failed to get ${item.iconName} form database`)
          })
      )
      // 无论单个图标获取是否成功，更新状态中心书签列表
      Promise.allSettled(promisesGetIconFiles).then(() => {
        this.$store.commit('setAppUserBookmarks', localBookmarks)
      })
    },
    /**
     * 首页背景相关
     */
    // 获取并保存默认背景信息
    async saveDefaultBackground () {
      // 获取默认背景信息
      const defBgInfo = await axios.get(this.urls.urlGetDefaultBackground)
        .then(response => {
          return Promise.resolve(response?.data)
        })
        .catch(error => {
          console.error(error)
          return Promise.reject(new Error('获取默认背景信息失败'))
        })
      // 获取背景图片信息
      const imgURL = defBgInfo?.bgImgURL?.replace('BASE_URL/', process.env.BASE_URL)
      const imgType = `image/${imgURL?.split('.').pop()}`
      const imgName = `${new Date().getTime()}.${imgType.split('/')[1]}`
      // 获取背景图片数据
      const imgData = await new Promise((resolve, reject) =>
        axiosRenewAccessToken().get(imgURL, {
          responseType: 'blob'
        }).then(response => {
          const imgBlob = response?.data
          if (!imgBlob?.size) {
            return reject(new Error('invalid image file of background'))
          }
          const reader = new FileReader()
          reader.addEventListener('load', () => {
            const tempImgData = {
              bgImgDataURL: reader.result,
              bgImgFile: new File([imgBlob], imgName, {
                type: imgType
              })
            }
            return resolve(tempImgData)
          })
          reader.addEventListener('error', () => {
            return reject(new Error('failed to convert image file of background'))
          })
          reader.addEventListener('abort', () => {
            return reject(new Error('image file conversion of background aborted'))
          })
          reader.readAsDataURL(imgBlob)
        }).catch(error => {
          // 获取图片失败的通知
          console.log(`failed to access image from ${imgURL}`)
          console.error(error)
          return resolve({
            bgImgDataURL: undefined,
            bgImgFile: undefined
          })
        }))
        .catch(error => {
          console.error(error)
        })
      // 完整背景信息
      const bgInfo = {
        bmTitleColor: defBgInfo.bmTitleColor,
        bgImgName: imgName,
        bgImgFile: imgData?.bgImgFile,
        bgImgDataURL: imgData?.bgImgDataURL
      }
      // 保存默认背景信息
      return this.$store.dispatch('saveAppUserBackground', bgInfo)
        .catch(error => {
          console.error(error)
          return Promise.reject(new Error('保存默认背景信息失败'))
        })
    },
    // 获取本地存储的背景信息
    getLocalBackground () {
      // 临时背景信息
      let localBackground = {}
      // 获取localStorage中的背景信息
      try {
        const tempStr = localStorage.getItem('appUserBackground')
        const tempObj = tempStr ? JSON.parse(tempStr) : {}
        localBackground = typeof tempObj === 'object' ? tempObj : {}
      } catch (error) {
        console.error(error)
        Notify({
          type: 'danger',
          message: '获取本地背景信息失败'
        })
        return
      }
      // 获取背景图片
      if (!localBackground.bgImgName) {
        this.$store.commit('setAppUserBackground', localBackground)
        return
      }
      bgImgDataStore.getItem(localBackground.bgImgName)
        .then(imgData => {
          Object.assign(localBackground, {
            bgImgDataURL: imgData?.dataURL,
            bgImgFile: imgData?.file
          })
        })
        .catch(() => {
          Notify({
            type: 'danger',
            message: '获取背景图片失败'
          })
        })
        .finally(() => {
          // 更新状态中心背景信息
          this.$store.commit('setAppUserBackground', localBackground)
        })
    },
    /**
     * 其它
     */
    // 响应尝试按钮点击事件
    onBtnTryClicked () {
      return this.isConnected ? this.isLoggedIn && this.tryToUpdateUserInfo() : this.tryToConnectServer()
    }
  }
}
</script>
