<template>
  <span class="btn-try">
    <Icon
      :class="{spinning: spinnable}"
      class="btn-try-icon"
      name="replay"
      @animationiteration="setSpinnable"
    />
  </span>
</template>

<style scoped>
.btn-try {
  display: inline-block;
  color: white;
  background-color: var(--primary-color);
  border-radius: 50%;
}

.btn-try-icon {
  width: 48px;
  font-size: 22px;
  line-height: 48px;
  text-align: center;
}

.spinning {
  animation: spinning 1.5s infinite;
}

@keyframes spinning {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>

<script>
import { Icon } from 'vant'

export default {
  name: 'BaseButtonTry',
  components: {
    Icon
  },
  props: {
    trying: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      spinnable: false
    }
  },
  watch: {
    trying (value) {
      this.spinnable = value || this.spinnable
    }
  },
  methods: {
    setSpinnable () {
      this.spinnable = this.trying
    }
  }
}
</script>
