import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store/index.js'
import PageLoading from '@/views/PageLoading.vue'
import PageError from '@/views/PageError.vue'
import Page404 from '@/views/Page404.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    redirect: '/404'
  },
  {
    path: '/404',
    name: 'Page404',
    component: Page404,
    meta: {
      level: 4
    }
  },
  // {
  //   path: '/loading',
  //   name: 'PageLoading',
  //   component: PageLoading
  // },
  // {
  //   path: '/error',
  //   name: 'PageError',
  //   component: PageError
  // },
  {
    path: '/',
    name: 'PageHome',
    component: () => ({
      component: import('@/views/PageHome.vue'),
      loading: PageLoading,
      error: PageError,
      delay: 200,
      timeout: 3000
    }),
    meta: {
      level: 1
    }
  },
  {
    path: '/login',
    name: 'PageLogin',
    component: () => ({
      component: import('@/views/PageLogin.vue'),
      loading: PageLoading,
      error: PageError,
      delay: 200,
      timeout: 3000
    }),
    meta: {
      level: 3
    }
  },
  {
    path: '/registration',
    name: 'PageRegistration',
    component: () => ({
      component: import('@/views/PageRegistration.vue'),
      loading: PageLoading,
      error: PageError,
      delay: 200,
      timeout: 3000
    }),
    meta: {
      level: 3
    }
  },
  {
    path: '/settings',
    name: 'PageSettings',
    component: () => ({
      component: import('@/views/PageSettings.vue'),
      loading: PageLoading,
      error: PageError,
      delay: 200,
      timeout: 3000
    }),
    meta: {
      level: 2
    },
    beforeEnter (to, from, next) {
      store.getters.isLoggedIn ? next() : next('/login')
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
