import axios from 'axios'
import store from '@/store/index.js'
import { RSAOAEPEncryptor } from '@/utils/RSAOAEPCryptor.js'
import { urlAPIPatchUserAccessToken } from '@/api/requestURLs.js'
import { AESCBCEncryptor, AESCBCDecryptor } from '@/utils/AESCBCCryptor.js'

// 请求拦截：设置请求头Authorization
function setHeaderAuthorization (store, config) {
  if (store.getters.isLoggedIn) {
    config.headers.authorization = `${store.state.appJWTString}`
  }
  return config
}

// 请求拦截：使用RSA-OAEP加密请求体
function encryptRequestWithRSAOAEP (pubKey, config) {
  config.data = RSAOAEPEncryptor(pubKey, config.data)
  return config
}

// 请求拦截：使用AES-CBC加密请求体
function encryptRequestWithAESCBC (store, config) {
  if (store.getters.isConnected) {
    const appAESSecrets = store.state.appAESSecrets
    config.data = AESCBCEncryptor(appAESSecrets.iv, appAESSecrets.key, config.data)
  }
  return config
}

// 响应拦截：检查是否有访问权限
function checkAccessAuthorization (store, error) {
  if (store.getters.isLoggedIn && error?.response?.status === 401) {
    store.dispatch('updateAppJWTString', undefined)
  }
  return error
}

// 响应拦截：检查服务器是否丢失连接信息
function checkServerConnection (store, error) {
  if (store.getters.isConnected && error?.response?.status === 403) {
    store.commit('setAppAESSecrets', undefined)
  }
  return error
}

// 响应拦截：使用AES-CBC解密响应体
function decryptResponseWithAESCBC (store, response) {
  if (store.getters.isConnected) {
    const appAESSecrets = store.state.appAESSecrets
    response.data = AESCBCDecryptor(appAESSecrets.iv, appAESSecrets.key, response.data)
  }
  return response
}

// 响应拦截：续签访问令牌JWT
let isRenewingAccessToken = false
function renewAccessToken (urlRenewAccessToken, response) {
  if (response.headers['Access-Token-Renewable'] && !isRenewingAccessToken) {
    // 更新是否正在刷新访问令牌
    isRenewingAccessToken = true
    // 向后端请求刷新访问令牌
    axiosBasic().patch(urlRenewAccessToken)
      .then(response => {
        // 更新本地访问访问令牌
        this.$store.dispatch('updateAppJWTString', response.headers.authorization)
        // 更新是否正在刷新访问令牌
        isRenewingAccessToken = false
      })
      .catch(error => {
        const response = error?.response
        console.error(`Error ${response?.status}: failed to renew access token.\nError: ${response?.data}`)
      })
  }
  return response
}

// 基础的请求与响应拦截
function axiosBasic () {
  // 创建axios实例
  const axiosInstance = axios.create({
    timeout: 3000,
    withCredentials: true
  })
  // 设置axios请求拦截器
  axiosInstance.interceptors.request.use(function (config) {
    setHeaderAuthorization(store, config)
    return config
  })
  // 设置axios响应拦截器
  axiosInstance.interceptors.response.use(undefined, function (error) {
    checkAccessAuthorization(store, error)
    checkServerConnection(store, error)
    return Promise.reject(error)
  })
  // 暴露axios实例
  return axiosInstance
}

// 刷新访问令牌
function axiosRenewAccessToken () {
  // 创建axios实例
  const axiosInstance = axiosBasic()
  // 设置axios响应拦截器
  axiosInstance.interceptors.response.use(function (response) {
    renewAccessToken(urlAPIPatchUserAccessToken, response)
    return response
  })
  // 暴露axios实例
  return axiosInstance
}

// 使用AES-CBC解密请求
function axiosDecryptWithAESCBC () {
  // 创建axios实例
  const axiosInstance = axiosRenewAccessToken()
  // 设置axios响应拦截器
  axiosInstance.interceptors.response.use(function (response) {
    decryptResponseWithAESCBC(store, response)
    return response
  }, function (error) {
    decryptResponseWithAESCBC(store, error.response)
    return Promise.reject(error)
  })
  // 暴露axios实例
  return axiosInstance
}

// 使用AES-CBC加密请求
function axiosEncryptWithAESCBC () {
  // 创建axios实例
  const axiosInstance = axiosDecryptWithAESCBC()
  // 设置axios请求拦截器
  axiosInstance.interceptors.request.use(function (config) {
    encryptRequestWithAESCBC(store, config)
    return config
  })
  // 暴露axios实例
  return axiosInstance
}

// 使用RSA-OAEP加密请求
function axiosEncryptWithRSAOAEP (pubKey) {
  // 创建axios实例
  const axiosInstance = axiosDecryptWithAESCBC()
  // 设置axios请求拦截器
  axiosInstance.interceptors.request.use(function (config) {
    encryptRequestWithRSAOAEP(pubKey, config)
    return config
  })
  // 暴露axios实例
  return axiosInstance
}

export {
  axiosBasic,
  axiosRenewAccessToken,
  axiosDecryptWithAESCBC,
  axiosEncryptWithAESCBC,
  axiosEncryptWithRSAOAEP
}
