import Vue from 'vue'
import Vuex from 'vuex'
import { localforage, iconDataStore, bgImgDataStore } from '@/utils/localForageStore.js'

Vue.use(Vuex)

const defaultState = {
  appAESSecrets: {
    iv: '',
    key: ''
  },
  appJWTString: '',
  appUserInfo: {
    uid: null,
    userName: null,
    nickName: null,
    createdAt: null
  },
  appUserBookmarks: [],
  appUserBackground: {
    bmTitleColor: '#000000',
    bgImgName: undefined,
    bgImgFile: undefined,
    bgImgDataURL: undefined
  }
}

export default new Vuex.Store({
  state: Object.assign({}, defaultState),
  getters: {
    isConnected: state => Boolean(state.appAESSecrets?.iv && state.appAESSecrets?.key),
    isLoggedIn: state => Boolean(state.appJWTString),
    isDatabaseSupported: () => localforage.supports(localforage.INDEXEDDB) || localforage.supports(localforage.WEBSQL)
  },
  mutations: {
    setAppAESSecrets (state, aesSecrets) {
      state.appAESSecrets = aesSecrets || defaultState.appAESSecrets
    },
    setAppJWTString (state, jwtString) {
      state.appJWTString = jwtString || defaultState.appJWTString
    },
    setAppUserInfo (state, userInfo) {
      state.appUserInfo = userInfo ? Object.assign({}, state.appUserInfo, userInfo) : defaultState.appUserInfo
    },
    setAppUserBookmarks (state, bookmarks) {
      state.appUserBookmarks = bookmarks || defaultState.appUserBookmarks
    },
    addAppUserBookmark (state, bookmark) {
      bookmark?.iconName ? state.appUserBookmarks.push(bookmark) : (console.error('invalid bookmark'))
    },
    removeAppUserBookmark (state, index) {
      typeof index === 'number' ? state.appUserBookmarks.splice(index, 1) : (console.error('invalid bookmark index'))
    },
    setAppUserBackground (state, background) {
      state.appUserBackground = Object.assign({}, defaultState.appUserBackground, background)
    }
  },
  actions: {
    updateAppJWTString ({ state, commit }, jwtString) {
      commit('setAppJWTString', jwtString)
      localStorage.setItem('appJWTString', state.appJWTString)
    },
    updateAppUserInfo ({ state, commit }, userInfo) {
      commit('setAppUserInfo', userInfo)
      localStorage.setItem('appUserInfo', JSON.stringify(state.appUserInfo))
    },
    updateAppUserBookmarks ({ commit }, bookmarks) {
      commit('setAppUserBookmarks', bookmarks)
      const bookmarksInfo = bookmarks.map(item => ({
        title: item.title,
        url: item.url,
        iconName: item.iconName
      }))
      localStorage.setItem('appUserBookmarks', JSON.stringify(bookmarksInfo))
    },
    async saveAppUserBookmarks ({ state, dispatch }, bookmarks) {
      const deleteArr = state.appUserBookmarks
        .filter(item => !bookmarks.includes(item))
        .map(item => iconDataStore.removeItem(item.iconName)
          .catch(error => {
            console.log(`failed to remove '${item.iconName}' in database`)
            console.error(error)
          }))
      await Promise.allSettled(deleteArr)
      const saveArr = bookmarks.map(item => {
        const iconData = {
          dataURL: item.iconSrc,
          file: item.iconFile
        }
        return iconDataStore.setItem(item.iconName, iconData)
          .catch(error => {
            console.log(`failed to save '${item.iconName}' into database`)
            console.error(error)
          })
      })
      return Promise.allSettled(saveArr).then(() => {
        dispatch('updateAppUserBookmarks', bookmarks)
      })
    },
    saveAppUserBookmark ({ state, commit }, bookmark) {
      if (!bookmark?.iconName) {
        return Promise.reject(new Error('invalid bookmark'))
      }
      commit('addAppUserBookmark', bookmark)
      localStorage.setItem('appUserBookmarks', JSON.stringify(state.appUserBookmarks))
      const iconName = bookmark.iconName
      const iconData = {
        dataURL: bookmark.iconSrc,
        file: bookmark.iconFile
      }
      return iconDataStore.setItem(iconName, iconData)
        .catch(error => {
          console.error(error)
          return Promise.reject(error)
        })
    },
    deleteAppUserBookmark ({ state, commit }, index) {
      const bookmark = state.appUserBookmarks[index]
      if (!bookmark) {
        return Promise.reject(new Error('invalid bookmark index'))
      }
      return iconDataStore.removeItem(bookmark.iconName)
        .then(() => {
          commit('removeAppUserBookmark', index)
          localStorage.setItem('appUserBookmarks', JSON.stringify(state.appUserBookmarks))
        })
        .catch(error => {
          console.error(error)
          return Promise.reject(error)
        })
    },
    updateAppUserBackground ({ state, commit }, background) {
      commit('setAppUserBackground', background)
      const backgroundDetail = {
        bmTitleColor: state.appUserBackground.bmTitleColor,
        bgImgName: state.appUserBackground.bgImgName
      }
      localStorage.setItem('appUserBackground', JSON.stringify(backgroundDetail))
    },
    async saveAppUserBackground ({ state, dispatch }, background) {
      const oldImgName = state.appUserBackground.bgImgName
      oldImgName && await bgImgDataStore.removeItem(oldImgName)
        .catch(error => {
          console.log(`failed to remove '${oldImgName}' in database`)
          console.error(error)
        })
      const imgName = background?.bgImgName
      if (!imgName) {
        return dispatch('updateAppUserBackground', background)
      }
      const imgData = {
        dataURL: background.bgImgDataURL,
        file: background.bgImgFile
      }
      return bgImgDataStore.setItem(imgName, imgData)
        .then(() => {
          dispatch('updateAppUserBackground', background)
        })
        .catch(error => {
          console.error(error)
          return Promise.reject(error)
        })
    }
  },
  modules: {
  }
})
